import { Component, OnInit, Output, EventEmitter, Inject, AfterViewInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { CookieService } from 'ngx-cookie-service';
import { SharedService } from '../../../../../shared/services/shared.service';
import { AlertService } from 'src/app/api/controllers/Alert';
import { ViolationService } from 'src/app/api/controllers/Violation';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-edit-violation',
  templateUrl: './edit-violation.component.html',
  styleUrls: ['./edit-violation.component.scss']
})
export class EditViolationComponent implements OnInit,AfterViewInit {

  @Output() outputEvent = new EventEmitter<any>();

  errorMessage = '';
  isError = false;

  isAccountAdded = false;

  violationData={
    primaryContactName:'',
    UnitTower:'',
    ParkingSlot:'',
    violationCategory:'',
    violationRuleNo:'',
    violationComments:'',
    violationNoticedOn:'',
    violationOccurence:'',
    unitOwnerName:'',
    penaltyAmt:'',
    raisedBy:'',
    invoiceNo:'',
    invoiceTrigger:''
  }

  ruleListData: any;
  allViolationCategory: any;
  violationCategory: FormControl;
  violationRule: FormControl;

  type;
  violationId:any;


  constructor(
    private violationService: ViolationService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private alertService: AlertService,
    private dialogRef: MatDialogRef<any>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) { }

  ngOnInit(): void {
    this.getViolationCategory();
  }

  ngAfterViewInit(){
    this.type = this.data.type;
    this.violationId=this.data.id;
    this.getviolationById(this.violationId);
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }


  getViolationRule(violationCateogoryId){    
    const _apartment_Id = parseInt(this.cookieService.get('apartmentId'));

      const params1 = {
        apartmentId: _apartment_Id,
        ViolationCategoryId: violationCateogoryId
      }

      this.violationService.getViolationRule(params1).subscribe((rule:any) => {

        this.ruleListData = rule;
      },
      error => {

        this.isError = true;

      })

  }

  getViolationCategory(){
    let params = {
      LookupTypeId: 78,
      active: 1
    }
    this.lookupService.getLookupValueByLookupTypeIdStatus(params).subscribe((res:any) => {
      this.allViolationCategory = res;

    })
  }

  onChangeCategoryID(event){
    this.getViolationRule(event);
  }

  getviolationById(id){

    this.alertService.getViolationById(id).subscribe((res:any) => {
      let getViolationData = res;

      this.getViolationRule(getViolationData[0].violationUserCategoryId);


  this.violationData={
    primaryContactName:'',
    UnitTower:'',
    ParkingSlot:'',
    violationCategory:getViolationData[0].violationUserCategoryId,
    violationRuleNo:'',
    violationComments:'',
    violationNoticedOn:'',
    violationOccurence:'',
    unitOwnerName:getViolationData[0].personName,
    penaltyAmt:getViolationData[0].penaltyAmount,
    raisedBy:getViolationData[0].raisedBy,
    invoiceNo:'',
    invoiceTrigger:''
  }

    })
  }


  onSubmit(form){

    const params = {
    ViolationStatusId: 1,
    Comments:this.violationData.violationComments,
    ApartmentId: parseInt(this.cookieService.get("apartmentId")),
    ApartmentBlockUnitId: 0,
    ViolationRuleId: parseInt(this.violationData.violationRuleNo),
    ViolationOccurrence: parseInt(this.violationData.violationOccurence),
    UpdatedOn: new Date().toISOString(),
    UpdatedBy: parseInt(this.cookieService.get("userId")),
    Raisedby: parseInt(this.violationData.raisedBy),
    PenaltyAmt: parseInt(this.violationData.penaltyAmt),
    NoticedOn: this.violationData.violationNoticedOn,
    IsDisputed: true,
    IsActive: true,
    InvoiceNo: parseInt(this.violationData.invoiceNo),
    InsertedOn: new Date().toISOString(),
    InsertedBy: parseInt(this.cookieService.get("userId")),
    Id: this.violationId,
    DisputedComment: ''
    }

    // this.violationService.updateViolations(params).subscribe((res: any) => {

    //   if (res) {
    //     this.sharedService.setAlertMessage(res.message);

    //           }
    //   else {
    //   }

    // });
  }

}
